<template>
    <div class="listData">
        <div class="list_wrap">
            <!-- positio -->
            <div class="position">
                <router-link to='/'>{{lang.home}}</router-link>
                <span> > </span>
                <router-link :to="langType!='cn'?('/'+langType+'/personageNew'):'/personageNew'">{{$route.meta.title[langType]}}</router-link>
            </div>
            <!-- list -->
            <div class="list">
                <ul class="clearfix">
                    <li v-for="(item,i) in menuSub" :key="i">
                        <span class='date'>{{item.gmtCreate | formateDate }}</span>
                        <router-link :to="'personageNew/'+item.id"> {{item.title}}</router-link>
                    </li>
                </ul>
            </div>

        </div>
    </div>
</template>
<script>
export default {
    name: 'list',
    data() {
        return {
            personalDetail: true,
            langType:"cn",
            lang:{},
        }
    },
    filters: {   //过滤
        formateDate: function (value) {
            var d = new Date(value);
            var y_ = d.getFullYear();
            var m_ = parseInt(d.getMonth() + 1) < 10 ? '0' + parseInt(d.getMonth() + 1) : parseInt(d.getMonth() + 1);
            var d_ = parseInt(d.getDate()) < 10 ? '0' + parseInt(d.getDate()) : d.getDate();
            return y_ + "-" + m_ + '-' + d_;
        }
    },
    props: {
        menuSub: {  //接收数据 
            type: Array,
            default() {
                return []
            }
        }
    },
    watch:{
        "$route.fullPath":{
            handler(){
                this.toggleLang();
            }
        }
    },
    methods:{
        toggleLang(){
            const _this = this;
            this.commonMethods.selectLangType(function(langType){
                _this.lang = _this.langOption[langType];
                _this.langType = langType;
            })
        }
    },
    mounted(){
        this.toggleLang();
    }
}
</script>


